body{
  background: url(../public/bac.jpg) no-repeat;
  overflow: hidden;
  background-size: cover;
  width: 100vw;
  height: 100vh;
  font-size: 10px;
}
.main-container{
  padding: 20px 0;
  height: 100vh;
 display: flex;
 flex-direction: column;
 justify-content: space-between;
}

.search{
  display: flex;
  justify-content: center;
}

.weather-info{
  margin-left: 20px;
  width: 100%;
  color: aliceblue;
  display: flex;
  justify-content: space-between;
}
.city{
  font-size: 4rem;
}
.temp{
  margin-top: -50px;
  font-size: 6rem;
}
.clouds{
  margin-top: 70%;
  font-size: 3.3rem;
  font-weight: bold;
  transform: rotate(-90deg);
}


.add-info{
  color: #fff;
  font-weight: bold;
  font-size: 1.5rem;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
  width: 40%;
  margin: 0 auto;
  margin-bottom: 50px;
}
@media (max-width: 1000px) {
  .add-info {
    width: 90%;
  }
}

h4{
  margin-top: -20px;
}
.add-info--data{
  text-align: center;
}
.input {
  font-weight: 500;
  font-size: 14px;
  height: 40px;
  border-radius: 10px;
  padding-left: 10px;
  border: none;
  border-bottom: 1px solid #e5e5e5;
  outline: none;
}

.input:focus {
  border-bottom: 1px solid #6941c6;
  -webkit-transition: 0.1s;
  transition: 0.5s;
}